/*

vim-hybrid theme by w0ng (https://github.com/w0ng/vim-hybrid)

*/

/*background color*/
.post-full-content pre code .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #1d1f21;
  -webkit-text-size-adjust: none;
}

/*selection color*/
.post-full-content pre code .hljs::selection,
.post-full-content pre code .hljs span::selection {
	background: #373b41;
}
.post-full-content pre code .hljs::-moz-selection,
.post-full-content pre code .hljs span::-moz-selection {
	background: #373b41;
}

/*foreground color*/
.post-full-content pre code .hljs,
.post-full-content pre code .hljs-setting .hljs-value,
.post-full-content pre code .hljs-expression .hljs-variable,
.post-full-content pre code .hljs-expression .hljs-begin-block,
.post-full-content pre code .hljs-expression .hljs-end-block,
.post-full-content pre code .hljs-class .hljs-params,
.post-full-content pre code .hljs-function .hljs-params,
.post-full-content pre code .hljs-at_rule .hljs-preprocessor {
  color: #c5c8c6;
}

/*color: fg_yellow*/
.post-full-content pre code .hljs-title,
.post-full-content pre code .hljs-function .hljs-title,
.post-full-content pre code .hljs-keyword .hljs-common,
.post-full-content pre code .hljs-class .hljs-title,
.post-full-content pre code .hljs-decorator,
.post-full-content pre code .hljs-tag .hljs-title,
.post-full-content pre code .hljs-header,
.post-full-content pre code .hljs-sub,
.post-full-content pre code .hljs-function {
  color: #f0c674;
}

/*color: fg_comment*/
.post-full-content pre code .hljs-comment,
.post-full-content pre code .hljs-javadoc,
.post-full-content pre code .hljs-output .hljs-value,
.post-full-content pre code .hljs-pi,
.post-full-content pre code .hljs-shebang,
.post-full-content pre code .hljs-doctype {
